<template>
  <section class="profile">
    <HeaderTop title="我的"></HeaderTop>
    <section class="profile-number">
      <router-link :to="userInfo._id ? '/userinfo':'/login'" class="profile-link">
        <div class="profile_image">
          <i class="fa fa-user-circle-o fa-4x" aria-hidden="true"></i>
        </div>
        <div class="user-info">
          <p
            class="user-info-top"
            :style="!userInfo.phone?true:'visibility:hidden'"
          >{{userInfo.name||'登录/注册'}}</p>
          <p>
            <span class="user-icon">
              <i class="iconfont iconshouji icon-mobile"></i>
            </span>
            <span class="icon-mobile-number">{{userInfo.phone||'暂无绑定手机号'}}</span>
          </p>
        </div>
        <span class="arrow">
          <i class="iconfont iconleft-copy"></i>
        </span>
      </router-link>
    </section>
    <section class="profile_info_data border-1px">
      <ul class="info_data_list">
        <a href="javascript:" class="info_data_link">
          <span class="info_data_top">
            <span>0.00</span>元
          </span>
          <span class="info_data_bottom">我的余额</span>
        </a>
        <a href="javascript:" class="info_data_link">
          <span class="info_data_top">
            <span>0</span>个
          </span>
          <span class="info_data_bottom">我的优惠</span>
        </a>
        <a href="javascript:" class="info_data_link">
          <span class="info_data_top">
            <span>0</span>分
          </span>
          <span class="info_data_bottom">我的积分</span>
        </a>
      </ul>
    </section>
    <section class="profile_my_order border-1px">
      <!-- 我的订单 -->
      <a href="javascript:" class="my_order">
        <span>
          <i class="iconfont icon-order-s"></i>
        </span>
        <div class="my_order_div">
          <span>我的订单</span>
          <span class="my_order_icon">
            <i class="iconfont iconleft-copy"></i>
          </span>
        </div>
      </a>
      <!-- 积分商城 -->
      <a href="javascript:" class="my_order">
        <span>
          <i class="iconfont icon-jifen"></i>
        </span>
        <div class="my_order_div">
          <span>积分商城</span>
          <span class="my_order_icon">
            <i class="iconfont iconleft-copy"></i>
          </span>
        </div>
      </a>
      <!-- 硅谷外卖会员卡 -->
      <a href="javascript:" class="my_order">
        <span>
          <i class="iconfont icon-vip"></i>
        </span>
        <div class="my_order_div">
          <span>饿了麽外卖会员卡</span>
          <span class="my_order_icon">
            <i class="iconfont iconleft-copy"></i>
          </span>
        </div>
      </a>
    </section>
    <section class="profile_my_order border-1px" style="margin-top:0">
      <!-- 服务中心 -->
      <a href="javascript:" class="my_order">
        <span>
          <i class="iconfont icon-fuwu"></i>
        </span>
        <div class="my_order_div noBorder">
          <span>服务中心</span>
          <span class="my_order_icon">
            <i class="iconfont iconleft-copy"></i>
          </span>
        </div>
      </a>
    </section>
    <section
      class="profile_my_order border-1px"
      style="background-color:transparent;width:80%;margin:10px auto 0"
    >
      <mt-button type="danger" style="width:100%;" v-if="userInfo._id" @click="logout">退出登录</mt-button>
    </section>
  </section>
</template>

<script>
import HeaderTop from "../../components/HeaderTop/HeaderTop.vue";
import { MessageBox, Toast } from "mint-ui";
import { mapState } from "vuex";

export default {
  /* data(){
    return{
      hidden:true
    }
  }, */
  components: {
    HeaderTop
  },
  methods: {
    // 点击按钮退出登录
    logout() {
      MessageBox.confirm("确认退出吗?").then(
        action => {
          // 请求退出
          this.$store.dispatch("logout");
          Toast({
            message: "已退出",
            iconClass: "iconfont iconkuangzhonggou"
          });
        },
        action => {
          console.log("点击了取消");
        }
      );
    }
  },
  computed: {
    ...mapState(["userInfo"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixins.styl';

.profile { // 我的
  width: 100%;
  overflow: hidden;

  .header {
    background-color: #009AFF;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 45px;
    box-shadow: 0 1px 4px rgba(0, 100, 165 0.2);

    .header_search {
      position: absolute;
      left: 15px;
      top: 50%;
      transform: translateY(-50%);
      width: 10%;
      height: 50%;

      .icon-sousuo {
        font-size: 25px;
        color: #fff;
      }
    }

    .header_title {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 50%;
      color: #fff;
      text-align: center;

      .header_title_text {
        font-size: 20px;
        color: #fff;
        display: block;
      }
    }

    .header_login {
      font-size: 14px;
      color: #fff;
      position: absolute;
      right: 15px;
      top: 50%;
      transform: translateY(-50%);

      .header_login_text {
        color: #fff;
      }
    }
  }

  .profile-number {
    margin-top: 44.5px;

    .profile-link {
      clearFix();
      position: relative;
      display: block;
      background: #009AFF;
      padding: 20px 10px;

      .profile_image {
        float: left;
        width: 62px;
        height: 62px;
        border-radius: 50%;
        overflow: hidden;
        vertical-align: top;
        box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);

        .fa-user-circle-o {
          background: #e4e4e4;
          color: #009AFF;
          font-size: 62px;
        }
      }

      .user-info {
        float: left;
        margin-top: 8px;
        margin-left: 15px;

        p {
          font-weight: 700;
          font-size: 18px;
          color: #fff;

          &.user-info-top {
            padding-bottom: 8px;
          }

          .user-icon {
            display: inline-block;
            margin-left: -5px;
            margin-right: -5px;
            width: 20px;
            height: 20px;

            .icon-mobile {
              font-size: 22px;
              vertical-align: text-top;
            }
          }

          .iconshouji {
            font-size: 14px;
            color: #fff;
          }
        }
      }

      .arrow {
        width: 12px;
        height: 12px;
        position: absolute;
        right: 15px;
        top: 40%;

        .iconleft-copy {
          color: #fff;
          font-size: 5px;
        }
      }
    }
  }

  .profile_info_data {
    // bottom-border-1px(#e4e4e4);
    border-bottom: 1px solid #f1f1f1;
    width: 100%;
    background: #fff;
    overflow: hidden;

    .info_data_list {
      clearFix();

      .info_data_link {
        float: left;
        width: 33%;
        text-align: center;
        border-right: 1px solid #f1f1f1;

        .info_data_top {
          display: block;
          width: 100%;
          font-size: 14px;
          color: #333;
          padding: 15px 5px 10px;

          span {
            display: inline-block;
            font-size: 30px;
            color: #009AFF;
            font-weight: 700;
            line-height: 30px;
          }
        }

        .info_data_bottom {
          display: inline-block;
          font-size: 14px;
          color: #666;
          font-weight: 400;
          padding-bottom: 10px;
        }
      }

      .info_data_link:nth-of-type(2) {
        .info_data_top {
          span {
            color: #009AFF;
          }
        }
      }

      .info_data_link:nth-of-type(3) {
        border: 0;

        .info_data_top {
          span {
            color: #009AFF;
          }
        }
      }
    }
  }

  .profile_my_order {
    top-border-1px(#e4e4e4);
    margin-top: 10px;
    background: #fff;

    &:before {
      height: 0;
    }

    .my_order {
      display: flex;
      align-items: center;
      padding-left: 15px;

      >span {
        display: flex;
        align-items: center;
        width: 20px;
        height: 20px;

        >.iconfont {
          margin-left: -10px;
          font-size: 25px;
        }

        .icon-order-s {
          color: #009AFF;
        }

        .icon-jifen {
          color: #009AFF;
        }

        .icon-vip {
          font-size: 28px;
          color: #009AFF;
        }

        .icon-fuwu {
          color: #009AFF;
        }
      }

      .my_order_div {
        width: 100%;
        border-bottom: 1px solid #f1f1f1;
        padding: 18px 10px 18px 0;
        font-size: 16px;
        color: #333;
        display: flex;
        justify-content: space-between;

        &.noBorder {
          border: none;
        }

        span {
          display: block;
        }

        .my_order_icon {
          width: 10px;
          height: 10px;

          .iconleft-copy {
            color: #bbb;
            font-size: 10px;
          }
        }
      }
    }
  }
}
</style>


